// app/layout.tsx  (RootLayout)
import type { Metadata } from "next";
import { Urbanist } from "next/font/google";
import localFont from "next/font/local";
import "./globals.css";

import Navbar from "@/components/navbar";
import Footer from "@/components/footer";
import CartSidebar from "@/components/cart-sidebar";
import { ThemeProvider } from "@/components/ui/theme-provider";

import { AuthProvider } from "@/context/AuthContext";
import { CartProvider } from "@/lib/cart-context";
import { CartUIProvider } from "@/lib/cart-ui-context";

import InitLocalStorage from "@/components/InitLocalStorage";

/* ----------------------------- Fuentes ----------------------------------- */
const urbanist = Urbanist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const simpson = localFont({
  src: "../public/fonts/Simpsonfont.ttf",
  display: "swap",
  variable: "--font-simpson",
});

const fontClasses = `${urbanist.variable} ${simpson.variable}`;

/* --------------------------- Metadatos ----------------------------------- */
export const metadata: Metadata = {
  title: "Kwik-E-Mart",
  description: "Bienvenido a Kwik-E-Mart, tu tienda de conveniencia en línea.",
};

/* ------------------------------------------------------------------------ */
/*                           Layout raíz                                    */
/* ------------------------------------------------------------------------ */
/**
 * **RootLayout**
 *
 * Estructura shared de todas las páginas:
 * - Carga fuentes, estilos globales y metadatos.
 * - Configura contextos (theme, auth, cart).
 * - Incluye componentes persistentes (Navbar, Sidebar, Footer).
 */
export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html
      lang="es"
      suppressHydrationWarning
      className={fontClasses}
      style={{ overflowX: "hidden" }}
    >
      <body className="antialiased flex flex-col min-h-screen overflow-x-hidden">
        {/* Tema (claro/oscuro) controlado por `data-theme` o sistema */}
        <ThemeProvider
          attribute="class"
          defaultTheme="system"
          enableSystem
          disableTransitionOnChange
        >
          {/* Auth global */}
          <AuthProvider>
            {/* UI del carrito (sidebar abierta/cerrada) */}
            <CartUIProvider>
              {/* Datos del carrito */}
              <CartProvider>
                {/* Init mock-storage (solo cliente) */}
                <InitLocalStorage />

                <Navbar />

                <main className="flex flex-col flex-1 pt-26 sm:pt-35 relative overflow-x-hidden">
                  <CartSidebar />
                  {children}
                </main>

                <Footer />
              </CartProvider>
            </CartUIProvider>
          </AuthProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}
